<template>
  <ViewTemplate
      :ref-name="$options.name"
  >
    <template v-slot:title>
      {{ $options.name }}
    </template>
    <template v-slot:content>
      <ContactsList
        :contacts="contacts"
      />
    </template>
  </ViewTemplate>
</template>

<script>
import ContactsList from "@/components/content/contacts/ContactsList";
import ViewTemplate from "@/views/templates/ViewTemplate";
import {ViewMixin} from "@/views/viewMixin";
import {_notEmpty} from "@/helpers/fakeLodash";

export default {
  name: "ContactsView",
  mixins: [
    ViewMixin
  ],
  components: {
    ViewTemplate,
    ContactsList
  },
  computed: {
    contacts () {
      return _notEmpty(this.$store.getters['GET_CONTACTS'])
    }
  },
  methods: {
    async getContacts() {
      return await this.$store.dispatch('fetchContacts')
    }
  },
  created() {
    if (!this.contacts) {
      this.getContacts()
    }
  }
}
</script>

